
import React from 'react';
import {useState,useEffect} from 'react'
import {dangerAlertShow} from './alerts.js'






function LoginPage(){



    // document.getElementById("loginForm").addEventListener("submit",async (e)=>{
        async function performLogin(e){
            
        e.preventDefault()
    
        let body={
            usermail:usermail,
            password:password
        }
    
        document.getElementById("spinner").classList.remove('d-none')
    
    const req = await fetch("login",{method:"POST",headers: {
        'Content-Type': 'application/json'
       
      },body:JSON.stringify(body)})
    
      document.getElementById("spinner").classList.add('d-none')
    
    
    
    if(req.status==200){
        let res = await req.json()
        switch(res.userType)
        {
            case "admin":{ window.location.href="/landingadmin";break}
            case "manager":{ window.location.href="/landingmanager";break}
            case "employee": {window.location.href="/landingemployee";break};
        }
       
    }
    else{
    const json = await req.json();
    dangerAlertShow(json.reason) }}

    const[usermail,setusermail]=useState("")
    const[password,setpassword]= useState("")


return(
  
    <div class="container">
    
    <form id="loginForm" class="border border-light p-5"  onSubmit={performLogin}>

        <p class="h4 mb-4 text-center">Sign in</p>
    
        <input type="email" name="usermail" class="form-control mb-4" placeholder="E-mail" value={usermail} onInput={(e)=>setusermail(e.target.value)}></input>
    
        <input type="password" name="password"  class="form-control mb-4" placeholder="Password" value={password} onInput={(e)=>setpassword(e.target.value)}></input>
    
        <div class="d-flex justify-content-between">
      s
            <div>
                <a href="/forgotPass">Forgot password?</a>
            </div>
        </div>
    
        <button class="btn btn-info btn-block my-4" type="submit">Sign in<span id="spinner" class="spinner-border spinner-border-sm ml-2 d-none" role="status" aria-hidden="true"></span></button >
    
        <div class="text-center">
            <p>Not a member?
                <a href="/register">Register</a>
            </p>
    

        </div>
    </form>


</div>

)

}

export default LoginPage