



    function dangerAlertShow(text){

        document.querySelector(".alert-danger").classList.remove("d-none")
        document.querySelector(".alert-danger").innerText=text
        setTimeout(()=> document.querySelector(".alert").classList.add("d-none"),1500)
    }


    function successAlertShow(text){

        document.querySelector(".alert-success").classList.remove("d-none")
        document.querySelector(".alert-success").innerText=text
        setTimeout(()=> document.querySelector(".alert-success").classList.add("d-none"),1500)
    }


function Alerts(){


return(

<>
    <div class="alert alert-success d-none" role="alert" style={{zIndex : 1031}}>
    This is a danger alert—check it out!
  </div>

<div class="alert alert-danger d-none" role="alert" style={{zIndex : 1032}}>
This is a danger alert—check it out!
</div>
</>
)

}

export   {Alerts,dangerAlertShow,successAlertShow}
