
import { useEffect, useState } from "react";
import {dangerAlertShow,successAlertShow} from './alerts.js'
import TicketItem from './ticketItem'

function StoresPanel(){

    const [tickets,setTickets]=useState([]);
    window.tickets=tickets

    useEffect(async ()=>{

        let req = await fetch('/tickets');
        if (req.status!=200){dangerAlertShow(await req.json().reason);return;}
        
        setTickets(await req.json())

        setInterval(async()=>{        let req = await fetch('/tickets');
        if (req.status!=200){dangerAlertShow(await req.json().reason);return;}
        
        setTickets(await req.json())},2000)

        
    
    },[])
    
    // setInterval(async()=>{        let req = await fetch('/tickets');
    // if (req.status!=200){dangerAlertShow(await req.json().reason);return;}
    
    // setTickets(await req.json())},5000)

    return(
        <div class="row">

        {tickets.map((t,i)=><TicketItem index={i} setTickets={setTickets} details={t}></TicketItem>)}

        </div>
        // stores.map((s)=><StoreItem id={s._id} key={s._id} setStores={setStores} name={s.name} address ={s.address} manager={s.manager} setmanagerList={setmanagerList} managerList={managerList} employeeList={employeeList} setemployeeList={setemployeeList}></StoreItem>)
       

    )


}

export default StoresPanel