import { useEffect,useState } from "react"
import Navbar from './navbar.js'
import AddNewFile from './addNewFile.js'
import StoresPanel from './storesPanel'
function LandingAdmin(){

    const [userMail,setUserMail]=useState("")
    const [fileList,setfileList]=useState("")
    const [showAddNewFile,setshowAddNewFile] = useState("none")

    useEffect(
    async ()=>{
        const request = await fetch('/userDetails');
        const response = await request.json();
        console.log(response)
        setUserMail(response.usermail)
    }

   ,[] )

     return(
 <>
 <Navbar usermail={userMail}></Navbar>
 <AddNewFile show={showAddNewFile} setshow={setshowAddNewFile} usermail={userMail}></AddNewFile>
 <div class='m-4'>hello</div>
        <StoresPanel></StoresPanel>
        <input name="" id="" class="btn btn-primary" type="button" onClick={()=>setshowAddNewFile("block")} value="New Ticket"/>
 </>
     )

}

export default LandingAdmin