
  import {dangerAlertShow,successAlertShow} from './alerts.js'
  function TicketItem(props){
function statusClass(){

    switch(props.status){
        case "Not Started":return "btn-info"
        case "Running":return "btn-warning"
        case "Failed": return "btn-danger"
        default: return "btn-success"
    }

}
let index=props.index
let setTickets=props.setTickets
props=props.details
async function startPayment()
{
    let req = await fetch('/triggerPayment',{method:"POST",headers: {
        'Content-Type': 'application/json'
        // 'Content-Type': 'application/x-www-form-urlencoded',
      },body:JSON.stringify({_id:props._id})})
    
}

async function deleteTicket()
{
  let req = await fetch('/removeTicket',{method:"POST",headers: {
    'Content-Type': 'application/json'
    // 'Content-Type': 'application/x-www-form-urlencoded',
  },body:JSON.stringify({_id:props._id})})

  if(req.status==200){
successAlertShow("Ticket Removed Successfully")

setTickets(t=>{let toReturn =[...t];toReturn.splice(index,1);return toReturn})
}
  else{dangerAlertShow("Unable to delete ticket")}
}
console.log(props)
    return(
        <div class="card m-4" style={{width: "20rem"}}>
<button onClick={deleteTicket}>


<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
  <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
  <path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
</svg>

</button>
  <div class="card-body text-center">
    <h5 class="card-title text-center">{props.title}</h5>
      <p class="card-text">{props.members.map((a)=>a.name).join(",")}</p>
      <p class="card-text">{props.date}</p>
      <p class="card-text">{props.zone}</p>
      <p class="card-text">{props.shift}</p>
    {/* <p class="card-text">{props.members.reduce((sum,obj)=>sum+=obj.name+",")}</p> */}
    <button type="button" class={"btn m-1 "+statusClass()} disabled>{props.status}</button>
    <button type="button" class="btn btn-primary m-1" disabled={props.status!="Awaiting Payment"} onClick={startPayment}>Start Payment</button>
    
  </div>
</div>
    )




}

export default TicketItem