import { useEffect, useState } from "react";
import {dangerAlertShow,successAlertShow} from './alerts.js'
import SingleMember from './singleMember.js'
function AddNewStore(props){
  const $ = window.$;
    const [addTicketForm,setaddTicketForm]= useState({title:"",zone:"",shift:"",date:"",vehicle:""})
    window.addTicketForm=addTicketForm
  const [members,setMembers]=useState([])
  window.members=members
    async function formSubmitted(e){
        
        e.preventDefault()

        let req = await fetch('/createTicket',{method:"POST",headers: {
          'Content-Type': 'application/json'
          // 'Content-Type': 'application/x-www-form-urlencoded',
        },body:JSON.stringify({...addTicketForm,members:members})})
        debugger
        if(req.status==200)
        {
          successAlertShow("Store Successfully Created")
         props.setshow('none')
         setaddTicketForm({title:"",zone:"",shift:"",date:"",vehicle:""})
         setMembers([])
        }
        else{
          let res = await req.json();
          dangerAlertShow(res.reson);
        }
      };
    


useEffect(()=>{         ;$('#datepicker').datepicker({
  
  uiLibrary: 'bootstrap4',format: 'dd/mm/yy'
  
});

$('#datepicker').change((i)=>{debugger;console.log(i);setaddTicketForm(e=>{return {...e,date:i.target.value}})})
},[])



    return(



        <div class="modal" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" style={{display:props.show}} >
        <div class="modal-dialog modal-xl" role="document">
          <div class="modal-content">


              <button type="button" class="close" data-dismiss="modal" aria-label="Close" onClick={()=>{props.setshow('none');         setaddTicketForm({title:"",zone:"",shift:"",date:"",vehicle:""});setMembers([])}}>
                <span aria-hidden="true">&times;</span>
              </button>
    
    
        
              <div class="col-12">
        <div class="form-group">
          <label for="">Ticket Title</label>
          <input type="text"
            class="form-control" value={addTicketForm.title} name="" id="name" aria-describedby="helpId" placeholder="" onChange={(i)=>setaddTicketForm(e=>{return {...e,title:i.target.value}})} required>
          </input>
        </div>
        </div>
    

            
            <form onSubmit={formSubmitted}>
              <div class="row">
                
              

              <div class="col col-6 form-group">
               <div class="form-group">
                 <label for="">Zone</label>
                 <select class="form-control" name="zone" id="" value={addTicketForm.zone} onChange={(i)=>setaddTicketForm(e=>{return {...e,zone:i.target.value}})} required>
                 <option value="" disabled>Select Zone</option>
                   <option value="Zone 1">Zone 1</option>
                   <option value="Zone 2">Zone 2</option>
                   <option value="Zone 3">Zone 3</option>
                   <option value="Zone 4">Zone 4</option>
                   <option value="Zone 5">Zone 5</option>
                   <option value="Zone 6">Zone 6</option>
                   <option value="Zone 7">Zone 7</option>
                   <option value="Zone 8">Zone 8</option>
                   <option value="Zone 9">Zone 9</option>
                   <option value="Zone 10">Zone 10</option>
                 </select>
               </div>

              </div>
              <div class="col col-6 form-group">
                 <label for="">Shift</label>
                 <select class="form-control" name="zone" id="" value={addTicketForm.shift} onChange={(i)=>setaddTicketForm(e=>{return {...e,shift:i.target.value}})} required>
                 <option value="" disabled>Select Shift</option>
                   <option value="Morning">Morning</option>
                   <option value="Evening">Evening</option>
                 </select>
               </div>
               <div class="col-6">
               <input id="datepicker" width="276" value={addTicketForm.date} required  ></input>
               </div>
               
              <div class="col-6 form-group">
                 <label for="">Vehicle</label>
                 <select class="form-control" name="vehicle" id="" value={addTicketForm.vehicle} onChange={(i)=>setaddTicketForm(e=>{return {...e,vehicle:i.target.value}})} required>
                 <option value="" disabled>Select Vehicle</option>
                   <option value="Morning">Gypsy</option>
                   <option value="Evening">Canter</option>
                 </select>
               </div>
               </div>
              {members.map((m,i)=><SingleMember setMembers={setMembers} index={i} name={m.name} gender={m.gender} nationality={m.nationality} idType={m.idType} idNo={m.idNo}></SingleMember>)}

              <input type="button" value="Add Member" onClick={()=>setMembers((e)=>[...e,{name:"",gender:"",nationality:"",idType:"",idNo:""}])} required></input>

              <button type="submit " class="btn btn-primary d-block">Submit</button>
            </form>
            </div>
     
          </div>
        </div>

    )





}

export default AddNewStore