import { useEffect, useState } from "react";
function SingleMember(props){

    useEffect(()=>console.log("member mounted"),[])
return (

    <div class="row">

    <div class="col-3">
        <div class="form-group">
          <label for="">Name</label>
          <input type="text"
            class="form-control" value={props.name} name="" id="name" aria-describedby="helpId" placeholder="" onChange={inp=>props.setMembers(mem=>{let toReturn =[...mem];toReturn[props.index].name=inp.target.value;return toReturn})} required>
          </input>
        </div>
        </div>

    <div class="col-2">
    <label for="">Gender</label>
                 <select class="form-control" name="zone" id="" value={props.gender} onChange={inp=>props.setMembers(mem=>{let toReturn =[...mem];debugger;toReturn[props.index].gender=inp.target.value;return toReturn})} required>
                 <option value="" disabled>Select Gender</option>
                   <option value="Male">Male</option>
                   <option value="Female">Female</option>
                 </select>
    </div>

    <div class="col-2">
    <label for="">Nationality</label>
                 <select class="form-control" name="zone" id="" value={props.nationality} onChange={inp=>props.setMembers(mem=>{let toReturn =[...mem];toReturn[props.index].nationality=inp.target.value;return toReturn})} required>
                 <option value="" disabled>Select Nation</option>
                   <option value="Indian">Indian</option>
                   <option value="Foreigner">Foreigner</option>
                 </select>
        </div>
        <div class="col-2">
    <label for="">ID Type</label>
                 <select class="form-control" name="zone" id="" value={props.idType} onChange={inp=>props.setMembers(mem=>{let toReturn =[...mem];toReturn[props.index].idType=inp.target.value;return toReturn})} required>                
                 <option value="" disabled>Select IdType</option>
<option value="Passport">Passport</option>
<option value="Aadhar">Aadhar</option>
<option value="Driving Licence">Driving Licence</option>
<option value="Voter ID">Voter ID</option>
<option value="PAN Card">PAN Card</option>
<option value="Office ID">Office ID</option>
<option value="Student ID">Student ID</option>
                 </select>
    </div>


        <div class="col-3">
        <div class="form-group">
          <label for="">ID No</label>
          <input type="text"
            class="form-control" name="" value={props.idNo} id="IdNo" aria-describedby="helpId" placeholder="" onChange={inp=>props.setMembers(mem=>{let toReturn =[...mem];toReturn[props.index].idNo=inp.target.value;return toReturn})} required>
          </input>
        </div>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close" onClick={inp=>props.setMembers(mem=>{let toReturn =[...mem];toReturn.splice(props.index,1);return toReturn})}>
                <span aria-hidden="true">&times;</span>
              </button>
    </div>


    </div>


)


}

export default SingleMember